/** @format */
import React from "react";
import {
    Grid,
    Paper,
    Typography,
    Stack,
    Box,
    Chip,
    IconButton,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import { Bookmark, BookmarkBorder } from "@mui/icons-material";
import useBookmarkStore from "../../Store/BookmarkStore";

const MangaHeader = ({ mangaData, theme }) => {
    const navigate = useNavigate();
    const addBookmark = useBookmarkStore((state) => state.addBookmark);
    const removeBookmark = useBookmarkStore((state) => state.removeBookmark);
    const hasBookmark = useBookmarkStore((state) =>
        state.hasBookmark(mangaData?.id)
    );

    const toggleBookmark = () => {
        if (hasBookmark) {
            removeBookmark(mangaData.id);
        } else {
            addBookmark(mangaData.id);
        }
    };

    return (
        <Grid container spacing={5} alignItems='flex-start'>
            {/* Cover */}
            {mangaData?.coverUrl && (
                <Grid size={{ xs: 12, md: 4 }}>
                    <Paper
                        elevation={8}
                        sx={{
                            borderRadius: 3,
                            overflow: "hidden",
                            maxWidth: { xs: "100%", sm: 320, md: 380 },
                            mx: "auto",
                            boxShadow: "0px 8px 30px rgba(0,0,0,0.5)",
                        }}>
                        <img
                            src={mangaData?.coverUrl}
                            alt={mangaData?.titleEN}
                            style={{
                                width: "100%",
                                height: "auto",
                                display: "block",
                            }}
                        />
                    </Paper>
                </Grid>
            )}

            {/* Text Info */}
            <Grid size={{ xs: 12, md: 12, lg: 8 }}>
                <Paper
                    elevation={6}
                    sx={{
                        width: "100%",
                        borderRadius: 3,
                        p: { xs: 2, sm: 3, md: 4 },
                        bgcolor: "rgba(255,255,255,0.05)",
                        backdropFilter: "blur(12px)",
                        boxShadow: "0px 8px 30px rgba(0,0,0,0.4)",
                    }}>
                    {/* Title + Bookmark */}
                    <Box
                        sx={{
                            width: "100%",
                            display: "flex",
                            justifyContent: "space-between",
                            alignItems: "flex-start",
                            flexWrap: "wrap",
                            mb: 2,
                            gap: 1,
                        }}>
                        <Box
                            sx={{
                                flexGrow: 1,
                                minWidth: "60%",
                                flexDirection: "row",
                                display: "flex",
                            }}>
                            <Typography
                                fontWeight='bold'
                                gutterBottom
                                sx={{
                                    fontSize: {
                                        xs: "1.5rem",
                                        sm: "2rem",
                                        md: "2.5rem",
                                    },
                                }}>
                                {mangaData?.titleEN || mangaData?.titleJP || "No Title"}
                            </Typography>
                            <IconButton
                                onClick={toggleBookmark}
                                aria-label='bookmark'
                                sx={{
                                    alignSelf: "flex-start",
                                }}>
                                {hasBookmark ? (
                                    <Bookmark
                                        sx={{
                                            fontSize: 36,
                                            color: "goldenrod",
                                        }}
                                    />
                                ) : (
                                    <BookmarkBorder
                                        sx={{ fontSize: 36, color: "white" }}
                                    />
                                )}
                            </IconButton>
                        </Box>
                    </Box>

                    {/* Status + Rating */}
                    <Stack
                        direction='row'
                        spacing={2}
                        alignItems='center'
                        flexWrap='wrap'
                        sx={{ mb: 2 }}>
                        <Chip
                            label={mangaData?.status}
                            onClick={() =>
                                navigate(
                                    `/tags?status=${
                                        mangaData?.status
                                            .charAt(0)
                                            .toUpperCase() +
                                        mangaData?.status.slice(1)
                                    }&sort=Latest`
                                )
                            }
                            sx={{
                                bgcolor: "secondary.main",
                                color: "white",
                                fontWeight: "bold",
                                borderRadius: "12px",
                            }}
                        />
                        <Chip
                            label={`⭐ ${Number(mangaData?.rating).toFixed(2)}`}
                            sx={{
                                bgcolor: "goldenrod",
                                color: "black",
                                fontWeight: "bold",
                                borderRadius: "12px",
                            }}
                        />
                    </Stack>

                    {/* Categories */}
                    <Box sx={{ mb: 3 }}>
                        <Typography
                            variant='body1'
                            gutterBottom
                            fontWeight='bold'>
                            Categories:
                        </Typography>
                        <Box
                            sx={{
                                display: "flex",
                                flexWrap: "wrap",
                                gap: 1,
                            }}>
                            {mangaData?.categories?.length > 0 ? (
                                mangaData.categories.map((cat, i) => (
                                    <Chip
                                        key={i}
                                        label={cat.label}
                                        variant='outlined'
                                        sx={{
                                            color: "white",
                                            borderColor: "primary.light",
                                            background:
                                                "linear-gradient(145deg, rgba(255,255,255,0.08), rgba(255,255,255,0.03))",
                                            fontWeight: "bold",
                                            "&:hover": {
                                                background:
                                                    "linear-gradient(145deg, rgba(255,255,255,0.2), rgba(255,255,255,0.1))",
                                            },
                                        }}
                                        onClick={() =>
                                            navigate(`/tags?tags=${cat.value}`)
                                        }
                                    />
                                ))
                            ) : (
                                <Typography variant='body2'>None</Typography>
                            )}
                        </Box>
                    </Box>

                    {/* Description */}
                    <Typography
                        variant='body1'
                        sx={{
                            whiteSpace: "pre-line",
                            lineHeight: 1.8,
                            maxHeight: { xs: 150, sm: 200, md: 250 },
                            overflowY: "auto",
                            pr: 1,
                            fontSize: { xs: "0.9rem", sm: "1rem" },
                        }}>
                        {mangaData?.description || "No description available."}
                    </Typography>
                </Paper>
            </Grid>
        </Grid>
    );
};

export default MangaHeader;
