/** @format */
import React from "react";
import {
    Grid,
    Card,
    CardActionArea,
    CardContent,
    Stack,
    Typography,
    Avatar,
    Box,
    Chip,
} from "@mui/material";
import MenuBookIcon from "@mui/icons-material/MenuBook";
import AccessTimeIcon from "@mui/icons-material/AccessTime";
import TranslateIcon from "@mui/icons-material/Translate";
import { format } from "timeago.js";
import useHistoryStore from "../../Store/ReadStore";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";

const ChapterCard = ({ ch, goToDetails }) => {
    const history = useHistoryStore((state) => state.history);

    // ✅ Check if this chapter is read
    const hasRead = history.some((m) => m.chapters.some((c) => c.id === ch.id));
    return (
        <Grid size={{ xs: 12, sm: 6, md: 4, lg: 3 }} key={ch.id}>
            <Card
                onClick={() => goToDetails(ch.id)}
                sx={(theme) => ({
                    cursor: "pointer",
                    height: "100%",
                    borderRadius: "20px",
                    bgcolor: hasRead
                        ? theme.palette.mode === "dark"
                            ? "rgba(0,128,0,0.15)" // ✅ subtle green tint if read
                            : "rgba(0,200,0,0.08)"
                        : theme.palette.mode === "dark"
                        ? "rgba(255,255,255,0.06)"
                        : "rgba(0,0,0,0.03)",
                    backdropFilter: "blur(12px)",
                    transition: "0.3s",
                    display: "flex",
                    "&:hover": {
                        transform: "translateY(-6px)",
                        boxShadow: `0px 8px 25px ${
                            theme.palette.mode === "dark"
                                ? "rgba(0,0,0,0.4)"
                                : "rgba(0,0,0,0.2)"
                        }`,
                        bgcolor: hasRead
                            ? theme.palette.mode === "dark"
                                ? "rgba(0,128,0,0.25)"
                                : "rgba(0,200,0,0.15)"
                            : theme.palette.mode === "dark"
                            ? "rgba(255,255,255,0.12)"
                            : "rgba(0,0,0,0.06)",
                    },
                })}>
                <CardActionArea sx={{ p: 2 }}>
                    <CardContent
                        sx={{
                            display: "flex",
                            flexDirection: "column",
                            gap: 1,
                        }}>
                        <Stack direction='row' spacing={2} alignItems='center'>
                            <Avatar
                                sx={{
                                    bgcolor: hasRead
                                        ? "success.main"
                                        : "primary.main",
                                    width: 48,
                                    height: 48,
                                }}>
                                {hasRead ? (
                                    <CheckCircleIcon />
                                ) : (
                                    <MenuBookIcon />
                                )}
                            </Avatar>
                            <Box sx={{ flexGrow: 1, maxWidth: "70%" }}>
                                <Typography
                                    variant='h6'
                                    noWrap
                                    sx={{
                                        textDecoration: hasRead
                                            ? "line-through"
                                            : "none", // ✅ strike-through if read
                                    }}>
                                    {ch.attributes.chapter
                                        ? `Chapter ${ch.attributes.chapter}`
                                        : "Oneshot"}
                                </Typography>
                                <Typography
                                    variant='body2'
                                    color='text.secondary'
                                    noWrap
                                    sx={{
                                        width: "100%",
                                        overflow: "hidden",
                                        textOverflow: "ellipsis",
                                    }}>
                                    {ch.attributes.title || "Untitled"}
                                </Typography>
                            </Box>
                        </Stack>
                        <Stack
                            direction='row'
                            spacing={1}
                            alignItems='center'
                            sx={{ mt: 1, justifyContent: "space-around" }}>
                            <Stack
                                direction='row'
                                spacing={1}
                                alignItems='center'>
                                <AccessTimeIcon fontSize='small' />
                                <Typography
                                    variant='body2'
                                    color='text.secondary'>
                                    {format(ch.attributes.readableAt)}
                                </Typography>
                            </Stack>
                            <Stack
                                direction='row'
                                spacing={1}
                                alignItems='center'>
                                <TranslateIcon fontSize='small' />
                                <Typography
                                    variant='caption'
                                    color='text.secondary'>
                                    EN
                                </Typography>
                            </Stack>
                            {hasRead && (
                                <Chip
                                    size='small'
                                    color='success'
                                    label='Read'
                                    sx={{ fontWeight: "bold" }}
                                />
                            )}
                        </Stack>
                    </CardContent>
                </CardActionArea>
            </Card>
        </Grid>
    );
};

export default ChapterCard;
