/** @format */
import React from "react";
import { Box, Typography, Button, Grid } from "@mui/material";
import InfiniteScroll from "react-infinite-scroll-component";
import ChapterCard from "./ChapterCard";
import ChaptersSkeletonList from "./ChapterSkeleton";

const MangaChapters = ({
    chapters,
    loadAllChapters,
    hasMoreChapters,
    order,
    setOrder,
    goToDetails,
    loading,
}) => {
    return (
        <Box sx={{ mt: 8 }}>
            <Box
                sx={{
                    display: "flex",
                    flex: 1,
                    flexDirection: "row",
                    justifyContent: "space-between",
                }}>
                <Typography variant='h4' gutterBottom fontWeight='bold'>
                    📚 Chapters
                </Typography>
                <Box
                    sx={{
                        mb: 2,
                        display: "flex",
                        gap: 2,
                        background:
                            "linear-gradient(to right, rgba(231,231,231,0.66), rgba(204,204,204,0.58))",
                        borderRadius: "12px",
                    }}>
                    <Button
                        variant={order === "asc" ? "contained" : "outlined"}
                        onClick={() => setOrder("asc")}>
                        Ascending
                    </Button>
                    <Button
                        variant={order === "desc" ? "contained" : "outlined"}
                        onClick={() => setOrder("desc")}>
                        Descending
                    </Button>
                </Box>
            </Box>

            <InfiniteScroll
                dataLength={chapters.length}
                next={loadAllChapters}
                hasMore={!!hasMoreChapters}
                loader={<ChaptersSkeletonList count={4} />}
                style={{ overflow: "visible" }}>
                {loading ? (
                    <ChaptersSkeletonList count={8} />
                ) : chapters && chapters.length > 0 ? (
                    <Grid container spacing={3} sx={{ mt: 2 }}>
                        {chapters.map((ch) => (
                            <ChapterCard
                                key={ch.id}
                                ch={ch}
                                goToDetails={goToDetails}
                            />
                        ))}
                    </Grid>
                ) : (
                    <Typography variant='body2'>No chapters found.</Typography>
                )}
            </InfiniteScroll>
        </Box>
    );
};

export default MangaChapters;
